  .scoreboard {
    border-radius: 75px;
      margin: 8% 20% 4%;
      border: 1px solid #cbccce;
    background: #ffffff;
    display: list-item;
    /* grid-template-rows: 2rem minmax(3rem, 1fr) minmax(3rem, 1fr) 2rem; */
    grid-template-columns: minmax(8rem, 1fr) 6rem;
    overflow: hidden;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      margin-bottom: 10px;
  }

  @media only screen and (min-width: 500px) {
    .scoreboard {
      grid-template-columns: minmax(8rem, 1fr) repeat(4, 2rem) 6rem;
    }
  }

  .scoreboard .title {
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid #cbccce;
    padding: 0 1rem;
    color: #7c7f84;
    font-size: 0.9rem;

  }
  @media only screen and (min-width: 500px) {
    .scoreboard .title {
      grid-column: 0 / span 2;
    }
  }
  .scoreboard .spacer {
    display: none;
  }
  @media only screen and (min-width: 500px) {
    .scoreboard .spacer {
      display: block;
      /*grid-column: 2 / span 4;*/
      grid-row: 1 / span 1;
      /*display: flex;*/
      -webkit-flex-direction: row;
              flex-direction: row;
      border-bottom: 1px solid #cbccce;
    }
    .scoreboard .spacer span {
      -webkit-flex: 1 1;
              flex: 1 1;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      /*font-size: 0.9rem;*/
    }
  }
  .scoreboard .total {
    grid-column: 2 / span 1;
    grid-row: 1 / span 1;
    border-bottom: 1px solid #cbccce;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 1rem;
    color: #57595c;
    font-size: 0.9rem;
  }
  @media only screen and (min-width: 500px) {
    .scoreboard .total {
      grid-column: 6 / span 1;
    }
  }
  .scoreboard .team {
    grid-column: 1 / span 2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding: 0.5rem 0 1rem;
  }
  @media only screen and (min-width: 500px) {
    .scoreboard .team {
      grid-column: 1 / span 6;
    }
  }
  .scoreboard .team.winner .team__total,
  .scoreboard .team.winner .team__details--name,
  .scoreboard .team.winner .team__score {
    color: #656c79;
  }
  .scoreboard .team.winner .team__total:after {
    content: "";
    position: absolute;
    margin: auto;
    right: 0;
    top: 0;
    bottom: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 10px 10px 0;
    border-color: transparent #656c79 transparent transparent;
  }
  .scoreboard .team__details {
    padding: 0 0.5rem;
    padding-right: 2rem;
    display: grid;
    grid-template-rows: auto 1fr;
    grid-template-columns: 4rem 1fr;
  }
  @media only screen and (min-width: 500px) {
    .scoreboard .team__details {
      padding-right: 4rem;
    }
  }
  .scoreboard .team__details--logo {
    grid-column: 1 / span 1;
    grid-row: 1 / span 2;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 0.5rem;
  }
  .scoreboard .team__details--logo img {
    max-width: 100%;
  }
  .scoreboard .team__details--name {
    grid-column: 2 / span 1;
    grid-row: 1 / span 1;
    padding: 0.5rem 0;
    padding-left: 1rem;
    font-weight: 700;
    color: #9a9faa;
  }
  .scoreboard .team__details--standings {
    grid-column: 2 / span 1;
    grid-row: 2 / span 1;
    padding-left: 1rem;
    color: #b5b9c2;
    font-size: 0.7rem;
  }
  .scoreboard .team__score {
    display: none;
  }
  @media only screen and (min-width: 500px) {
    .scoreboard .team__score {
      display: block;
      width: 2rem;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      font-size: 0.8rem;
      color: #9a9faa;
    }
  }
  .scoreboard .team__total {
    position: relative;
    width: 6rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 1rem;
    font-size: 2rem;
    font-weight: 900;
    color: #9a9faa;
  }

  .scoreboard .note {
    grid-column: 1 / span 2;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 1rem;
    color: #b5b9c2;
    font-size: 0.75rem;
    border-top: 1px solid #cbccce;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  @media only screen and (min-width: 500px) {
    .scoreboard .note {
      grid-column: 1 / span 6;
    }
  }

