.typing {
  position: relative;

}

typing::after {
    content: "|";
    position: absolute;
    right: 0;
    width: 100%;
    -webkit-animation: typing 4s steps(16) forwards,
      caret 1s infinite;
            animation: typing 4s steps(16) forwards,
      caret 1s infinite;
}

@-webkit-keyframes typing {
	to { width: 0 }
}

@keyframes typing {
	to { width: 0 }
}
@-webkit-keyframes caret {
	50% { color: transparent }
}
@keyframes caret {
	50% { color: transparent }
}

